@use '../mixins' as *;
@use '../variables' as *;

.section-header__title {
    font-size: 2rem;
    position: relative;
    color: var(--text-color);
    text-transform: capitalize;
    letter-spacing: 2px;

    @include medium {
        font-size: 2.5rem;
    }

    span.coloured {
        color: var(--red);
    }
}

